.hero {
  position: relative;
  background-image: url("../img/background/cam-mobile.png");
  background-color: $color-white-smoke-darknest;
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;

  padding: 60px 43px 60px 45px;

  @media (min-width: $vp-768) {
    background-image: url("../img/background/cam-tablet.png");
    padding: 80px 40px 80px 40px;
  }

  @media (min-width: $vp-1920) {
    padding: 64px 240px 64px 240px;
    background-image: url("../img/background/cam-desktop.png");
  }
}

.hero__text {
  margin: 0;
  font-size: 38px;
  line-height: 48px;
  max-width: 392px;
  margin-bottom: 40px;

  font-weight: 600;
  color: $color-black;
  word-wrap: break-word;

  @media (min-width: $vp-768) {
    max-width: 605px;
    font-size: 40px;
    line-height: 50px;
  }

  @media (min-width: $vp-1200) {
    margin-right: 0;
    margin-left: 0;
    font-size: 50px;
    line-height: 65px;
    max-width: 705px;
  }
}

.hero__button {
  margin: 0;
  padding: 13px 76px;

  border-radius: 8px;
  font-size: 18px;

  @media (min-width: $vp-1200) {
    padding: 13px 96px;
  }
}
