@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/interregular.woff2") format("woff2"), url("../fonts/interregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/intersemibold.woff2") format("woff2"), url("../fonts/intersemibold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.button {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Inter", "Arial", sans-serif;
  color: #ffffff;
  text-decoration: none;
  border-radius: 8px;
  background-color: #ee2523;
  border: none;
  cursor: pointer;
}
.button:hover {
  opacity: 0.9;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  opacity: 0.7;
}
.button:focus-visible {
  outline: 3px #48b774 solid;
  outline-offset: 2px;
}
.button:disabled {
  color: #ffffff;
  background-color: #e5e5e5;
  cursor: default;
}

.page {
  height: 100%;
}

.page-body {
  height: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  font-family: "Inter", "Arial", sans-serif;
  color: #000000;
  background-color: #f2f2f2;
}

.content-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 390px;
  min-height: 100%;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

.main-header {
  display: flex;
  padding: 10px;
}
.main-header.no-js {
  padding: 0;
}
@media (min-width: 1200px) {
  .main-header {
    justify-content: space-between;
    padding: 30px 60px;
  }
}
@media (min-width: 1920px) {
  .main-header {
    justify-content: space-between;
    padding: 11px 240px;
  }
}

.main-header__navigation {
  display: flex;
}

.main-header-login {
  width: 23px;
  align-self: center;
  height: 21px;
  background-image: url("../img/svg-images/user-login-avatar.svg");
}
.main-header-login:hover {
  opacity: 0.6;
}
.main-header-login:active {
  opacity: 0.4;
}
@media (min-width: 1200px) {
  .main-header-login {
    display: none;
  }
}

.main-header__logo {
  display: flex;
  flex-shrink: 0;
  width: 199px;
  height: 43px;
  margin: auto auto auto 0;
}
@media (min-width: 768px) {
  .main-header__logo {
    margin-right: auto;
  }
}
@media (min-width: 1920px) {
  .main-header__logo {
    margin-right: 97px;
  }
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.5;
}
.main-header__logo:focus-visible {
  outline: none;
  opacity: 0.8;
}

.main-header__toggle {
  display: flex;
  flex-direction: column;
  align-self: center;
  margin-left: 15%;
}
@media (min-width: 1200px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle:hover {
  opacity: 0.6;
}
.main-header__toggle:active {
  opacity: 0.3;
}
.main-header__toggle:focus-visible {
  outline: 3px #766357 solid;
  outline-offset: 2px;
}
.main-header__toggle:disabled {
  opacity: 0.2;
}

.main-header__button {
  margin: auto 0;
  padding: 9px 23px;
}

.main-container {
  flex-grow: 1;
}

.navigation__list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: none;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ee2523;
  transform: translateY(-100%);
  transition: transform 0.3s ease;
  gap: 1px;
}
@media (min-width: 1200px) {
  .navigation__list {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    align-self: center;
    background-color: #ffffff;
    transform: translateY(0);
    transition: none;
    gap: 20px;
  }
}
@media (min-width: 1920px) {
  .navigation__list {
    gap: 36px;
  }
}
.is-open .navigation__list {
  display: grid;
  min-width: 320px;
  transform: translateY(0);
}
@media (min-width: 1200px) {
  .is-open .navigation__list {
    display: flex;
  }
}
.no-js .navigation__list {
  position: relative;
  display: grid;
  transform: translateY(0);
  transition: none;
}

.navigation__list-dropd {
  min-width: 225px;
  display: none;
  margin: 0;
  padding: 0;
  padding-top: 31px;
  z-index: 3;
  list-style: none;
}
@media (min-width: 1200px) {
  .navigation__list-dropd {
    flex-direction: column;
    position: absolute;
    top: 25px;
    gap: 20px;
    background-color: #ffffff;
  }
}

.navigation__item-dropd::before {
  position: absolute;
  top: -10px;
  content: "";
  width: 80%;
  height: 1px;
  background-color: #eeeeee;
}

.navigation__item {
  position: relative;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 1200px) {
  .navigation__item {
    display: flex;
    justify-content: center;
    box-shadow: none;
  }
}
@media (max-width: 1199px) {
  .navigation__item:last-child {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 90%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 1200px) {
  .navigation__item:nth-child(4n-1) {
    grid-column-start: 4;
  }
}

.navigation__link {
  position: relative;
  display: block;
  padding: 17px 67px 12px 67px;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #ffffff;
  text-transform: capitalize;
  text-decoration: none;
}
@media (min-width: 1200px) {
  .navigation__link {
    padding: 0;
    max-width: 250px;
    font-size: 16px;
    line-height: 24px;
    color: #1f1f1f;
  }
}
@media (min-width: 1200px) {
  .navigation__link:hover {
    opacity: 0.7;
  }
}
.navigation__link:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .navigation__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
}
.navigation__link:focus-visible {
  outline: 3px #766357 solid;
}
@media (min-width: 1200px) {
  .navigation__link:focus-visible {
    color: #81b3d2;
    background-color: #f2f2f2;
    outline: none;
  }
}

.navigation__link-dropd {
  padding: 10px 28px;
}

.navigation__link-telephone {
  color: #ffffff;
}
@media (min-width: 1200px) {
  .navigation__link-telephone {
    color: #ee2523;
  }
}

@media (min-width: 1200px) {
  .navigation__item:hover .navigation__list-dropd {
    display: flex;
  }
}

.hero {
  position: relative;
  background-image: url("../img/background/cam-mobile.png");
  background-color: #f8f8f8;
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 60px 43px 60px 45px;
}
@media (min-width: 768px) {
  .hero {
    background-image: url("../img/background/cam-tablet.png");
    padding: 80px 40px 80px 40px;
  }
}
@media (min-width: 1920px) {
  .hero {
    padding: 64px 240px 64px 240px;
    background-image: url("../img/background/cam-desktop.png");
  }
}

.hero__text {
  margin: 0;
  font-size: 38px;
  line-height: 48px;
  max-width: 392px;
  margin-bottom: 40px;
  font-weight: 600;
  color: #000000;
  word-wrap: break-word;
}
@media (min-width: 768px) {
  .hero__text {
    max-width: 605px;
    font-size: 40px;
    line-height: 50px;
  }
}
@media (min-width: 1200px) {
  .hero__text {
    margin-right: 0;
    margin-left: 0;
    font-size: 50px;
    line-height: 65px;
    max-width: 705px;
  }
}

.hero__button {
  margin: 0;
  padding: 13px 76px;
  border-radius: 8px;
  font-size: 18px;
}
@media (min-width: 1200px) {
  .hero__button {
    padding: 13px 96px;
  }
}

.burger-button {
  position: relative;
  z-index: 3;
  order: 3;
  width: 23px;
  height: 22px;
  padding: 0;
  background-color: inherit;
  border: none;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.no-js .burger-button {
  display: none;
}

.burger-button__line {
  display: block;
  width: 23px;
  height: 4px;
  background-color: #ee2523;
  transition: all 0.3s ease;
}
.burger-button__line:not(:last-child) {
  margin-bottom: 5px;
}

.is-open .burger-button__line {
  width: 27px;
  background-color: #ffffff;
}
.is-open .burger-button__line:nth-child(2) {
  position: relative;
  top: 48%;
  transform: rotate(45deg);
}
.is-open .burger-button__line:nth-child(3) {
  display: none;
}
.is-open .burger-button__line:nth-child(4) {
  position: relative;
  top: 10%;
  transform: rotate(-45deg);
}

.services {
  padding: 60px 46px 60px 43px;
}
@media (min-width: 768px) {
  .services {
    padding: 40px 64px;
  }
}
@media (min-width: 1920px) {
  .services {
    padding: 64px 230px;
  }
}

.services__title {
  font-size: 32px;
  font-weight: 600;
  line-height: 48px;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
}
@media (min-width: 1200px) {
  .services__title {
    font-size: 30px;
    line-height: 45px;
  }
}

.services__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 20px;
}
@media (min-width: 1200px) {
  .services__list {
    gap: 60px;
  }
}

.services__item {
  display: flex;
  flex-wrap: wrap;
  padding: 40px 30px;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.12);
  max-width: 440px;
}

.services__image-wrapper {
  margin-bottom: 24px;
}

.services__item-title {
  margin: 0;
  margin-bottom: 12px;
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
}
@media (min-width: 768px) {
  .services__item-title {
    font-size: 24px;
    line-height: 36px;
  }
}

.services__item-description {
  margin: 0;
  margin-bottom: 47px;
  font-size: 16px;
  line-height: normal;
  color: rgba(0, 0, 0, 0.5);
  max-width: 330px;
}
@media (min-width: 768px) {
  .services__item-description {
    max-width: 274px;
  }
}
@media (min-width: 1200px) {
  .services__item-description {
    max-width: 380px;
  }
}

.services__about-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 1200px) {
  .services__about-wrapper {
    flex-direction: row;
  }
}

.services__about {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-bottom: 12px;
}
@media (min-width: 1200px) {
  .services__about {
    margin-bottom: 0;
  }
}

.services__about-price {
  margin: 0;
  font-size: 48px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.5px;
}

.services__about-description {
  font-size: 16px;
  line-height: 21px;
}

.services__about-rub {
  text-decoration: underline;
  text-underline-offset: 3px;
}

.services__about-text {
  font-size: 30px;
}

.services__link {
  text-decoration: none;
  margin-left: auto;
  padding: 12px 18px;
  text-align: center;
}
@media (max-width: 1200px) {
  .services__link {
    width: 100%;
  }
}

.paid-button {
  position: fixed;
  top: 52%;
  right: 0;
  padding: 16px 50px 16px 18px;
  font-weight: 600;
  color: #ffffff;
  text-transform: none;
  border: 1px solid #ee2523;
  border-radius: 8px 0 0 8px;
}
.paid-button::after {
  content: "";
  position: absolute;
  background-image: url("../img/svg-images/icon-wallet.svg");
  top: 50%;
  right: 19px;
  width: 24px;
  height: 24px;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .paid-button {
    padding: 32px 64px 32px 19px;
  }
}

.paid-button-text {
  display: none;
}
@media (min-width: 768px) {
  .paid-button-text {
    display: inline;
  }
}